.dropdown {
	width: 240px;
	border-radius: 3px;
	min-height: 100px;
	position: absolute;
	background: #fff;
	top: 25px;
	left: 0;
	z-index: 50;
	border: 1px solid rgba(155, 155, 155, 0.199);
	box-shadow: 0 0 5px 1px rgba(155, 155, 155, 0.479);
}
.dropdown h4 {
	font-size: 14px;
	text-align: center;
	padding: 10px 5px;
	color: rgb(126, 126, 126);
}
.dropdown hr {
	height: 1px;
	background: rgba(155, 155, 155, 0.479);
	border-radius: 1px;
	margin: 0 10px;
}
.dropdown-menus {
	margin: 5px 0;
}
.dropdown-menus li a,
.dropdown-menus li button {
	display: block;
	width: 100%;
	background: transparent;
	text-align: left;
	padding: 10px;
	color: #172b4d;
	font-size: 14px;
	cursor: pointer;
}
.dropdown-menus li a:hover,
.dropdown-menus li button:hover {
	background: rgba(155, 155, 155, 0.199);
}
