.addCol {
	min-width: 272px;
	max-width: 272px;
	padding: 10px;
	background: rgba(255, 255, 255, 0.4);
	border-radius: 5px;
	cursor: pointer;
	transition: background 0.2s ease-in 0s;
}
.addCol:hover {
	background: rgba(255, 255, 255, 0.5);
}
.addCol__placeholder {
	color: #fff;
	font-size: 16px;
}
.addCol__placeholder i {
	margin-right: 10px;
	font-size: 14px;
	margin-left: 8px;
}
.addCol.active .addCol__placeholder {
	display: none;
}
.addCol.active {
	background: rgb(245, 245, 245);
	padding: 5px;
}
.addCol__form {
	display: none;
}
.addCol.active .addCol__form {
	display: block;
}
.addCol__input {
	height: 35px;
	width: 100%;
	padding: 5px 10px;
	font-size: 16px;
	border: 2px solid rgba(62, 62, 255, 0.863);
	border-radius: 3px;
	margin-bottom: 5px;
}
.addCol__btns {
	display: flex;
}
.addCol__buttonAdd {
	background: rgba(62, 62, 255, 0.863);
	border-radius: 4px;
	padding: 0 10px;
	height: 30px;
	color: #fff;
	font-size: 16px;
	margin-right: 15px;
}
.addCol__buttonClose {
	color: #42526e;
	font-size: 20px;
	background: none;
}
