.list__item {
	background: #fff;
	border-radius: 5px;
	font-size: 13px;
	line-height: 16px;
	transition: background 0.2s ease-in 0s;
	display: flex;
	box-shadow: 0px 3px 3px -3px gray;
}
.list__item:hover {
	background: rgb(250, 250, 250);
}
.list__item a {
	width: 100%;
	display: block;
	padding: 7px 8px;
	color: #172b4d;
}
.itemDeleteButton {
	padding: 0 8px;
	background: none;
	color: gray;
	font-size: 13px;
	opacity: 0;
	transition: all 0.2s ease-in 0s;
}
.itemDeleteButton:hover {
	background: rgb(236, 236, 236);
}
.list__item:hover .itemDeleteButton {
	opacity: 1;
}
.itemIcons {
	margin-top: 5px;
	margin-left: 5px;
	color: gray;
	font-size: 10px;
}
.itemIcons i {
	margin-right: 10px;
}
.item-tags {
	margin-bottom: 6px;
	display: flex;
	flex-wrap: wrap;
}
.item-tags span {
	width: 40px;
	height: 8px;
	margin-right: 4px;
	border-radius: 4px;
	display: block;
	margin-bottom: 4px;
}
