.overlay {
	height: 100%;
	width: 100%;
	position: fixed;
	top: 0;
	left: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	background: rgba(0, 0, 0, 0.3);
	overflow-y: auto;
	z-index: 20;
}
.addPopup {
	width: 700px;
	background: #f4f5f7;
	margin-top: 80px;
	margin-bottom: 80px;
	position: relative;
	padding: 30px 50px;
	border-radius: 4px;
	overflow-y: auto;
	min-height: 300px;
}
.addPopup__titleInput {
	width: 100%;
	font-size: 18px;
	height: 32px;
	padding: 0 8px;
	border-radius: 3px;
	background: rgba(9, 30, 66, 0.04);
	color: #172b4d;
	margin-bottom: 5px;
}
.addPopup__titleInput:focus {
	border: 2px solid rgba(62, 62, 255, 0.863);
	background: #fff;
}
.addPopup-title {
	position: relative;
	margin-bottom: 25px;
}
.addPopup-title i {
	position: absolute;
	top: 6px;
	left: -33px;
	font-size: 20px;
	color: #42526e;
}
.addPopup-title__wh {
	margin-left: 10px;
	font-size: 16px;
	color: #5e6c84;
}
.addPopup-tag {
	margin-left: 10px;
	margin-bottom: 30px;
}
.addPopup-tag label {
	color: #5e6c84;
}
.addPopup-colors {
	margin-top: 10px;
	display: flex;
}
.addPopup-colors span {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 40px;
	height: 32px;
	margin-right: 5px;
	border-radius: 4px;
	cursor: pointer;
}
.addPopup-colors span i {
	color: #fff;
}
.green {
	background: #61bd4f;
}
.yellow {
	background: #f2d600;
}
.orange {
	background: #ff9f1a;
}
.red {
	background: #eb5a46;
}
.purple {
	background: #c377e0;
}
.blue {
	background: #0079bf;
}
.addPopup-detail {
	margin-bottom: 20px;
}
h3 {
	color: #172b4d;
	font-size: 18px;
	font-weight: 500;
}
.addPopup-detail__title {
	position: relative;
}
.addPopup-detail__title i {
	position: absolute;
	top: -2px;
	left: -33px;
	font-size: 20px;
	color: #42526e;
}
.addPopup-detail__title h3 {
	margin-bottom: 15px;
}
.addPopup-detail__input {
	width: 100%;
	height: 108px;
	padding: 8px;
	border-radius: 3px;
	background: rgba(9, 30, 66, 0.04);
	color: #172b4d;
	font-size: 14px;
}
.addPopup-detail__input:focus {
	border: 2px solid rgba(62, 62, 255, 0.863);
	background: #fff;
}
.addPopup__btns {
	display: flex;
}
.addPopup__buttonSave {
	background: rgba(62, 62, 255, 0.863);
	border-radius: 4px;
	padding: 0 10px;
	height: 30px;
	color: #fff;
	font-size: 16px;
	margin-right: 15px;
}
.addPopup__buttonClose {
	color: #42526e;
	font-size: 20px;
	background: none;
}
.addPopup__close {
	position: absolute;
	right: 15px;
	top: 15px;
	color: #42526e;
	font-size: 20px;
	cursor: pointer;
}
