.list {
	min-width: 272px;
	max-width: 272px;
	min-height: 50px;
	max-height: 100%;
	background: rgb(245, 245, 245);
	border-radius: 5px;
	padding: 10px;
}
.title {
	font-size: 16px;
	font-weight: 700;
	margin-right: 5px;
	height: 25px;
	padding: 5px 8px;
	width: 100%;
	background: none;
	border-radius: 2px;
}
.title:focus {
	border: 2px solid rgba(62, 62, 255, 0.863);
	background: #fff;
	font-weight: 500;
}
.list-top {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 10px;
	max-width: 300px;
}
.lists {
	overflow-y: auto;
	max-height: calc(100vh - 180px);
	min-height: 1px;
}
.lists li {
	margin-bottom: 8px;
}
.list-top i {
	color: gray;
	transition: all 0.4s ease-in-out 0s;
	font-size: 12px;
	border-radius: 3px;
	padding: 5px;
}
.list-top i:hover {
	color: #000;
	background: rgba(155, 155, 155, 0.479);
}
.context-menu {
	position: relative;
}
.list-top h2 {
	font-size: 16px;
	font-weight: 700;
	margin-right: 5px;
	margin-left: 8px;
	width: 100%;
	height: 100%;
	padding: 4px 0;
	cursor: pointer;
	margin-bottom: 1px;
}
.lists::-webkit-scrollbar-track {
	-webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
	border-radius: 10px;
	background-color: #f5f5f5;
}

.lists::-webkit-scrollbar {
	width: 8px;
	background-color: #f5f5f5;
}

.lists::-webkit-scrollbar-thumb {
	border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
	background-color: rgb(194, 194, 194);
}
