@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,500;0,700;1,400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@500&display=swap);
/*Обнуление*/
* {
	padding: 0;
	margin: 0;
	border: 0;
}
*,
*:before,
*:after {
	box-sizing: border-box;
}
:focus,
:active {
	outline: none;
}
a:focus,
a:active {
	outline: none;
}
nav,
footer,
header,
aside {
	display: block;
}
html,
body {
	height: 100%;
	width: 100%;
	font-size: 100%;
	line-height: 1;
	font-size: 14px;
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}
input,
button,
textarea {
	font-family: inherit;
}
input::-ms-clear {
	display: none;
}
button {
	cursor: pointer;
}
button::-moz-focus-inner {
	padding: 0;
	border: 0;
}
a,
a:visited {
	text-decoration: none;
}
a:hover {
	text-decoration: none;
}
ul li {
	list-style: none;
}
img {
	vertical-align: top;
}
h1,
h2,
h3,
h4,
h5,
h6 {
	font-size: inherit;
	font-weight: inherit;
}
/*--------------------*/
body {
	font-family: "Roboto", sans-serif;
}

.App {
	width: 100%;
	height: 100vh;
	background: #56ab2f; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(
		to right,
		#a8e063,
		#56ab2f
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.header {
	width: 100%;
	height: 50px;
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 24px;
	font-weight: 700;
	color: rgb(247, 247, 247);
	background: rgba(0, 0, 0, 0.2);
	font-family: "Raleway", sans-serif;
}
.content {
	padding: 15px;
	width: 100%;
	height: 100%;
	display: flex;
	align-items: flex-start;
	overflow-x: auto;
	position: relative;
}
.content .list {
	margin-right: 10px;
}
.app-container {
	width: 100%;
	height: calc(100% - 50px);
	padding: 15px;
	display: flex;
	align-items: flex-start;
	overflow-x: auto;
	position: relative;
}
.app-container .list {
	margin-right: 10px;
}

.list {
	min-width: 272px;
	max-width: 272px;
	min-height: 50px;
	max-height: 100%;
	background: rgb(245, 245, 245);
	border-radius: 5px;
	padding: 10px;
}
.title {
	font-size: 16px;
	font-weight: 700;
	margin-right: 5px;
	height: 25px;
	padding: 5px 8px;
	width: 100%;
	background: none;
	border-radius: 2px;
}
.title:focus {
	border: 2px solid rgba(62, 62, 255, 0.863);
	background: #fff;
	font-weight: 500;
}
.list-top {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 10px;
	max-width: 300px;
}
.lists {
	overflow-y: auto;
	max-height: calc(100vh - 180px);
	min-height: 1px;
}
.lists li {
	margin-bottom: 8px;
}
.list-top i {
	color: gray;
	transition: all 0.4s ease-in-out 0s;
	font-size: 12px;
	border-radius: 3px;
	padding: 5px;
}
.list-top i:hover {
	color: #000;
	background: rgba(155, 155, 155, 0.479);
}
.context-menu {
	position: relative;
}
.list-top h2 {
	font-size: 16px;
	font-weight: 700;
	margin-right: 5px;
	margin-left: 8px;
	width: 100%;
	height: 100%;
	padding: 4px 0;
	cursor: pointer;
	margin-bottom: 1px;
}
.lists::-webkit-scrollbar-track {
	-webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
	border-radius: 10px;
	background-color: #f5f5f5;
}

.lists::-webkit-scrollbar {
	width: 8px;
	background-color: #f5f5f5;
}

.lists::-webkit-scrollbar-thumb {
	border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
	background-color: rgb(194, 194, 194);
}

.list__item {
	background: #fff;
	border-radius: 5px;
	font-size: 13px;
	line-height: 16px;
	transition: background 0.2s ease-in 0s;
	display: flex;
	box-shadow: 0px 3px 3px -3px gray;
}
.list__item:hover {
	background: rgb(250, 250, 250);
}
.list__item a {
	width: 100%;
	display: block;
	padding: 7px 8px;
	color: #172b4d;
}
.itemDeleteButton {
	padding: 0 8px;
	background: none;
	color: gray;
	font-size: 13px;
	opacity: 0;
	transition: all 0.2s ease-in 0s;
}
.itemDeleteButton:hover {
	background: rgb(236, 236, 236);
}
.list__item:hover .itemDeleteButton {
	opacity: 1;
}
.itemIcons {
	margin-top: 5px;
	margin-left: 5px;
	color: gray;
	font-size: 10px;
}
.itemIcons i {
	margin-right: 10px;
}
.item-tags {
	margin-bottom: 6px;
	display: flex;
	flex-wrap: wrap;
}
.item-tags span {
	width: 40px;
	height: 8px;
	margin-right: 4px;
	border-radius: 4px;
	display: block;
	margin-bottom: 4px;
}

.addItem {
	color: gray;
	margin-top: 8px;
	width: 100%;
	font-size: 14px;
	background: transparent;
	transition: color 0.4s ease-in-out 0s;
	display: block;
	text-align: center;
}
.addItem:hover {
	color: #000;
}
.addItem i {
	margin-right: 10px;
	font-size: 12px;
}

.dropdown {
	width: 240px;
	border-radius: 3px;
	min-height: 100px;
	position: absolute;
	background: #fff;
	top: 25px;
	left: 0;
	z-index: 50;
	border: 1px solid rgba(155, 155, 155, 0.199);
	box-shadow: 0 0 5px 1px rgba(155, 155, 155, 0.479);
}
.dropdown h4 {
	font-size: 14px;
	text-align: center;
	padding: 10px 5px;
	color: rgb(126, 126, 126);
}
.dropdown hr {
	height: 1px;
	background: rgba(155, 155, 155, 0.479);
	border-radius: 1px;
	margin: 0 10px;
}
.dropdown-menus {
	margin: 5px 0;
}
.dropdown-menus li a,
.dropdown-menus li button {
	display: block;
	width: 100%;
	background: transparent;
	text-align: left;
	padding: 10px;
	color: #172b4d;
	font-size: 14px;
	cursor: pointer;
}
.dropdown-menus li a:hover,
.dropdown-menus li button:hover {
	background: rgba(155, 155, 155, 0.199);
}

.addCol {
	min-width: 272px;
	max-width: 272px;
	padding: 10px;
	background: rgba(255, 255, 255, 0.4);
	border-radius: 5px;
	cursor: pointer;
	transition: background 0.2s ease-in 0s;
}
.addCol:hover {
	background: rgba(255, 255, 255, 0.5);
}
.addCol__placeholder {
	color: #fff;
	font-size: 16px;
}
.addCol__placeholder i {
	margin-right: 10px;
	font-size: 14px;
	margin-left: 8px;
}
.addCol.active .addCol__placeholder {
	display: none;
}
.addCol.active {
	background: rgb(245, 245, 245);
	padding: 5px;
}
.addCol__form {
	display: none;
}
.addCol.active .addCol__form {
	display: block;
}
.addCol__input {
	height: 35px;
	width: 100%;
	padding: 5px 10px;
	font-size: 16px;
	border: 2px solid rgba(62, 62, 255, 0.863);
	border-radius: 3px;
	margin-bottom: 5px;
}
.addCol__btns {
	display: flex;
}
.addCol__buttonAdd {
	background: rgba(62, 62, 255, 0.863);
	border-radius: 4px;
	padding: 0 10px;
	height: 30px;
	color: #fff;
	font-size: 16px;
	margin-right: 15px;
}
.addCol__buttonClose {
	color: #42526e;
	font-size: 20px;
	background: none;
}

.overlay {
	height: 100%;
	width: 100%;
	position: fixed;
	top: 0;
	left: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	background: rgba(0, 0, 0, 0.3);
	overflow-y: auto;
	z-index: 20;
}
.addPopup {
	width: 700px;
	background: #f4f5f7;
	margin-top: 80px;
	margin-bottom: 80px;
	position: relative;
	padding: 30px 50px;
	border-radius: 4px;
	overflow-y: auto;
	min-height: 300px;
}
.addPopup__titleInput {
	width: 100%;
	font-size: 18px;
	height: 32px;
	padding: 0 8px;
	border-radius: 3px;
	background: rgba(9, 30, 66, 0.04);
	color: #172b4d;
	margin-bottom: 5px;
}
.addPopup__titleInput:focus {
	border: 2px solid rgba(62, 62, 255, 0.863);
	background: #fff;
}
.addPopup-title {
	position: relative;
	margin-bottom: 25px;
}
.addPopup-title i {
	position: absolute;
	top: 6px;
	left: -33px;
	font-size: 20px;
	color: #42526e;
}
.addPopup-title__wh {
	margin-left: 10px;
	font-size: 16px;
	color: #5e6c84;
}
.addPopup-tag {
	margin-left: 10px;
	margin-bottom: 30px;
}
.addPopup-tag label {
	color: #5e6c84;
}
.addPopup-colors {
	margin-top: 10px;
	display: flex;
}
.addPopup-colors span {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 40px;
	height: 32px;
	margin-right: 5px;
	border-radius: 4px;
	cursor: pointer;
}
.addPopup-colors span i {
	color: #fff;
}
.green {
	background: #61bd4f;
}
.yellow {
	background: #f2d600;
}
.orange {
	background: #ff9f1a;
}
.red {
	background: #eb5a46;
}
.purple {
	background: #c377e0;
}
.blue {
	background: #0079bf;
}
.addPopup-detail {
	margin-bottom: 20px;
}
h3 {
	color: #172b4d;
	font-size: 18px;
	font-weight: 500;
}
.addPopup-detail__title {
	position: relative;
}
.addPopup-detail__title i {
	position: absolute;
	top: -2px;
	left: -33px;
	font-size: 20px;
	color: #42526e;
}
.addPopup-detail__title h3 {
	margin-bottom: 15px;
}
.addPopup-detail__input {
	width: 100%;
	height: 108px;
	padding: 8px;
	border-radius: 3px;
	background: rgba(9, 30, 66, 0.04);
	color: #172b4d;
	font-size: 14px;
}
.addPopup-detail__input:focus {
	border: 2px solid rgba(62, 62, 255, 0.863);
	background: #fff;
}
.addPopup__btns {
	display: flex;
}
.addPopup__buttonSave {
	background: rgba(62, 62, 255, 0.863);
	border-radius: 4px;
	padding: 0 10px;
	height: 30px;
	color: #fff;
	font-size: 16px;
	margin-right: 15px;
}
.addPopup__buttonClose {
	color: #42526e;
	font-size: 20px;
	background: none;
}
.addPopup__close {
	position: absolute;
	right: 15px;
	top: 15px;
	color: #42526e;
	font-size: 20px;
	cursor: pointer;
}

.loader {
	position: absolute;
	top: 50%;
	left: 50%;
	width: 142px;
	height: 40px;
	margin: -20px 0 0 -71px;
	-webkit-filter: contrast(20);
	        filter: contrast(20);
}
.dot {
	position: absolute;
	width: 16px;
	height: 16px;
	top: 12px;
	left: 15px;
	-webkit-filter: blur(0px);
	        filter: blur(0px);
	background: #fff;
	border-radius: 50%;
	transform: translateX(0);
	animation: dot 2.8s infinite;
}
.dots {
	transform: translateX(0);
	margin-top: 12px;
	margin-left: 31px;
	animation: dots 2.8s infinite;
}
.dots span {
	display: block;
	float: left;
	width: 16px;
	height: 16px;
	margin-left: 16px;
	-webkit-filter: blur(0px);
	        filter: blur(0px);
	background: #fff;
	border-radius: 50%;
}
@keyframes dot {
	50% {
		transform: translateX(96px);
	}
}
@keyframes dots {
	50% {
		transform: translateX(-31px);
	}
}

